body {
  color: #fcfcfc;
  width: 100vw;
  background-image: url("background-mobile.8f8b84e0.jpg");
  background-size: contain;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

@media screen and (min-width: 768px) {
  body {
    background-image: url("background-tablet.7414c78f.jpg");
  }
}

@media screen and (min-width: 1200px) {
  body {
    background-image: url("background-desktop.372d502d.jpg");
  }
}

@media screen and (max-width: 767px) {
  ::-webkit-scrollbar {
    width: 0;
  }
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
