}

.section {
  justify-content: center;
  display: flex;
}

.button {
  text-align: center;
  color: #000;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  padding: 12px 36px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  transition: background-color .25s ease-in, border-color .25s ease-in;
}

.button:hover {
  cursor: pointer;
  background-color: #0a8817;
}

.button--transparent {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
}

.button--transparent:hover {
  background-color: rgba(0, 0, 0, 0);
  border-color: #0a8817;
}

.icon {
  transition: fill .25s ease-in;
}

.link {
  color: #fcfcfc;
  text-decoration: none;
  transition: color .25s ease-in;
}

.link:hover {
  cursor: pointer;
  color: #f8d24e;
}

.mobile-menu {
  visibility: hidden;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background-color: #84a178;
  flex-direction: column;
  padding: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  transform: translateX(100vw);
}

.mobile-menu__footer {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
  display: flex;
}

.mobile-menu__icon {
  width: 40px;
  height: 40px;
  fill: #fcfcfc;
}

.mobile-menu__icon:hover {
  cursor: pointer;
  fill: #f8d24e;
}

.mobile-menu__list {
  margin-bottom: 114px;
}

.mobile-menu__item {
  text-align: center;
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 32px;
}

.mobile-menu__link {
  color: #fcfcfc;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.mobile-menu__link--black {
  color: #000;
}

.mobile-menu__button {
  width: 152px;
  align-self: center;
}

